export default class Export {

	constructor( element ) {
		this.element = element;
		this.Table = new Table( element.querySelector( '.ac-table' ) );

		this.getExportJsonButton().setAttribute( 'disabled', true );
		this.initEvents();
	}

	doAjaxExportCall() {
		return jQuery.ajax( {
			url : ajaxurl,
			method : 'get',
			data : {
				action : 'acp-export-php',
				encoder : 'php-export',
				list_screen_id : this.Table.getSelectedListScreens(),
				response_type : 'string',
				_ajax_nonce : this.element.querySelector( '[name="_ajax_nonce"]' ).value
			}
		} );
	}

	getExportJsonButton() {
		return this.element.querySelector( '[data-export="json"]' );
	}

	initEvents() {
		this.Table.element.querySelectorAll( 'input' ).forEach( el => {
			el.addEventListener( 'change', () => {
				this.getExportJsonButton().setAttribute( 'disabled', true );

				if ( this.Table.getSelectedListScreens().length > 0 ) {
					this.getExportJsonButton().removeAttribute( 'disabled' );
				}
			} );
		} );
	}

}

class Table {
	constructor( element ) {
		this.element = element;
	}

	getSelectedListScreens() {
		let ids = [];
		this.element.querySelectorAll( '[name="list_screen_ids[]"]:checked' ).forEach( el => {
			ids.push( el.value );
		} );

		return ids;
	}

}