import Synchronisation from "./tools/synchronisation";
import Export from "./tools/export";
import Import from "./tools/import";

document.addEventListener( "DOMContentLoaded", function() {
	AdminColumns.Tools = {};

	let SyncSection = document.querySelector( '.ac-section.-syncronisation' );
	if ( SyncSection ) {
		AdminColumns.Tools.Synchronisation = new Synchronisation( SyncSection );
	}

	let ExportSection = document.querySelector( '.ac-section.-export' );
	if ( ExportSection ) {
		AdminColumns.Tools.Export = new Export( ExportSection );
	}

	let ImportSection = document.querySelector( '.ac-section.-import' );
	if ( ImportSection ) {
		AdminColumns.Tools.Import = new Import( ImportSection );
	}

} );