export default class Synchronisation {
	constructor( element ) {
		this.element = element;
		let table = element.querySelector( '.ac-table' );
		let filter = element.querySelector( '.ac-sync-filter' );

		if ( table ) {
			this.Table = new Table( table );
		}

		if ( table && this.Table ) {
			this.Filter = new Filter( filter, this.Table );
		}

		this.init();
	}

	getSyncButton() {
		return this.element.querySelector( '.button-primary' );
	}

	checkButtonState() {
		const Button = this.getSyncButton();
		if ( !Button || !this.Table ) {
			return;
		}

		Button.setAttribute( 'disabled', true );

		if ( this.Table.getSelectedSyncableItems().length > 0 ) {
			Button.removeAttribute( 'disabled' );
		}
	}

	init() {
		const Button = this.getSyncButton();

		if ( Button ) {
			Button.setAttribute( 'disabled', true );
			this.checkButtonState();
			this.Table.table.querySelectorAll( 'input[type=checkbox]' ).forEach( input => {
				input.addEventListener( 'change', () => this.checkButtonState() );
			} );
		}

	}

}

class Table {

	constructor( el ) {
		this.table = el;
	}

	checkNoResults( className ) {
		let row = this.table.querySelector( 'tfoot' );

		row.style.display = 'none';
		if ( !this.hasItemsForFilter( className ) ) {
			row.style.display = 'table-footer-group';
		}
	}

	hasItemsForFilter( className ) {
		let selector = className ? `tbody tr.${className}` : `tbody tr`;
		return this.table.querySelectorAll( selector ).length > 0;
	}

	getSelectedSyncableItems() {
		return this.table.querySelectorAll( 'input[type=checkbox]:checked' );
	}

	resetSelection() {
		this.table.querySelectorAll( 'input[checkbox]' ).forEach( input => input.checked = false );
	}

	filterRows( className ) {
		if ( !className ) {
			this.table.querySelectorAll( 'tbody tr' ).forEach( el => el.style.display = 'table-row' );
			this.checkNoResults( className );
			return;
		}

		this.table.querySelectorAll( 'tbody tr' ).forEach( el => el.style.display = 'none' );
		this.table.querySelectorAll( `tbody tr.${className}` ).forEach( el => el.style.display = 'table-row' );
		this.checkNoResults( className );
	}

}

class Filter {

	constructor( filter, table ) {
		this.filter = filter;
		this.table = table;
		this.init();
		this.refresh();
	}

	refresh() {
		let current = this.filter.querySelector( 'a.current' );
		if ( current ) {
			this.setFilter( current );
		}
	}

	setFilter( link ) {
		this.filter.querySelectorAll( 'a' ).forEach( el => el.classList.remove( 'current' ) );
		link.classList.add( 'current' );

		let filterClass = link.dataset.filter ? link.dataset.filter : '';
		this.table.filterRows( filterClass );
	}

	init() {
		this.filter.querySelectorAll( 'a' ).forEach( link => {
			link.addEventListener( 'click', e => {
				e.preventDefault();
				this.setFilter( link );
			} );
		} );

		let firstItem = this.filter.querySelector( 'li:first-child a' );
		if ( firstItem && !firstItem.classList.contains( 'current' ) ) {
			let filter = firstItem.dataset.filter;
			if ( this.table.hasItemsForFilter( filter ) ) {
				this.setFilter( firstItem );
			}
		}

	}

}